html.map,
body.map{
	width: 100%;
	height: 100%;
	
	margin: 0;
	padding: 0;
	
	*,
	*:before, 
	*:after
	{
		-moz-box-sizing: border-box;
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
}
	
	#map-wrapper{
		position:relative;
		
		h1.filters_toggle{
			position:absolute;
			top:10px;
			right:10px;
			background-color:#fff;
		}
		
		#map-container{
			height:100%;
			position:absolute !important;
			top:0;
			bottom:0;
			left:0;
			right:0;
			z-index:2;
		}
	}
	
	.flexbox-parent
	{
		width: 100%;
		height: 100%;
	
		display: flex;
		flex-direction: column;
		
		justify-content: flex-start; /* align items in Main Axis */
		align-items: stretch; /* align items in Cross Axis */
		align-content: stretch; /* Extra space in Cross Axis */
	}
	.flexbox-item-grow
	{
		flex: 1; /* same as flex: 1 1 auto; */
	}
	
	.fill-area
	{
		display: flex;
		flex-direction: row;
		
		justify-content: flex-start; /* align items in Main Axis */
		align-items: stretch; /* align items in Cross Axis */
		align-content: stretch; /* Extra space in Cross Axis */
		
	}
	.fill-area-content
	{
		/* Needed for when the area gets squished too far and there is content that can't be displayed */
		overflow: hidden; 
	}


#map_legend{
	background:#fff;
	padding:10px;
	
	.legend_item{
		margin-bottom:5px;
		list-style:none;
		text-transform:capitalize;
		
		.legend_swatch{
			width: 20px;
			height: 20px;
			display: inline-block;
			vertical-align: middle;
			margin-right:5px;
		}
	}
}

#tt{
	position:absolute;
	z-index:1000;
	background-color:@white;
	padding:10px;
	max-width:200px;
}
.marker{
		transition: all 0.35s ease;
		-ms-transform-origin: bottom center; /* IE 9 */
		-webkit-transform-origin: bottom center; /* Chrome, Safari, Opera */
		transform-origin: bottom center;
		position:absolute;
		cursor:pointer;
		width:61.5px;
		height: 50px;
		top: -50px;
  		left: -30.75px;
		transform: scale(0);
	&.in{
		transform: scale(.7);
		
		&.hover{
			transform: scale(1);
			filter: contrast(2);
		}
	}
	i{
		pointer-events: none;
		position:absolute;
		cursor:pointer;
		width:20px;
		height: 20px;
		top:10px;
		left: 20px;
		color: #fff;
		font-size: 21px;
		line-height: .8;
	}
}